// import { SimpleInput } from "react-bootstrap-input";
// import RangePicker from "react-range-picker";
import { useState, useRef } from "react";
import DatePicker from "react-date-picker";
import Select from "react-select";
import { useDispatch } from "react-redux";
import { AddContact } from "../actions";

const Todo = () => {
  const dispatch = useDispatch();
  const [username, setUsername] = useState("");
  const [gender, setGender] = useState("");
  const [error, setError] = useState({});
  const [hobby, setHobby] = useState(new Array(3).fill(false));
  const [age, setAge] = useState(18);
  const [dob, setDob] = useState();
  const [taskName, setTaskName] = useState("");
  const [status, setStatus] = useState(null);
  const options = [
    { value: "Active", label: "Active" },
    { value: "Inactive", label: "Inactive" },
  ];
  const statusInputRef = useRef();

  const changeAge = (e) => {
    setAge(e.target.value);
  };
  const changeUsername = (e) => {
    setUsername(e.target.value);
  };
  const changeGender = (e) => {
    setGender(e.target.value);
  };

  const changeHobby = (position, name) => {
    const updatedHobby = hobby.map((item, index) =>
      index === position ? !item : item
    );
    setHobby(updatedHobby);
  };

  const changeDob = (Date) => {
    setDob(Date);
  };
  const changeTaskName = (e) => {
    setTaskName(e.target.value);
  };
  const changeStatus = (e) => {
    setStatus(e?.value);
  };
  const resetForm = () => {
    setUsername("");
    setGender("");
    setHobby(new Array(3).fill(false));
    setAge(18);
    setTaskName("");
    setError({});
    setDob();
    statusInputRef.current.clearValue();
  };
  const handleSubmit = (e) => {
    e.preventDefault();

    const value = [];
    hobby.forEach((item, index) => {
      if (item) {
        if (index === 0) value[0] = "Sport";
        if (index === 1) value[1] = "Reading";
        if (index === 2) value[2] = "Music";
      }
    });
    let formIsValid = true;
    const hobbyvalue = value.filter((x) => x.length !== 0);
    let errors = {};
    if (!username.match(/^[a-zA-Z ]*$/) || username.length > 15) {
      formIsValid = false;
      errors["username"] =
        "Only letters with white Space allowed,Maximum allowed length is 15 ";
      setError(errors);
    }

    const user = {
      username: username,
      gender: gender,
      hobby: hobbyvalue.join(", "),
      age: age,
      dob: dob.toDateString(),
      taskName: taskName,
      status: status,
    };
    if (formIsValid) {
      dispatch(AddContact(user));
      console.log(user);
      resetForm();
    }
  };
  return (
    <>
      <h1 className="p-3 mb-2 bg-primary text-white text-center rounded">
        Todo Task using Redux
      </h1>
      <form method="post" className="my-5 p-5 border border-secondary rounded">
        <div className="form-group">
          <label for="username">Username</label>
          {/* <SimpleInput
            formObj={username}
            name="username"
            placeholder="Enter your user name"
            onChange={changeUsername}
            required
            validated
            maxlength="15"
            pattern={"/^[a-zA-Zs]*$/g"}
            errorMessage="Please provide a valid email address"
          /> */}
          <input
            type="text"
            className="form-control"
            id="username"
            placeholder="Enter Username"
            value={username}
            onChange={changeUsername}
          ></input>
          <span style={{ color: "red" }}>{error["username"]}</span>
        </div>
        <div className="form-group">
          <label for="username">Gender</label>
          <br />
          <input
            className="pr-2"
            type="radio"
            checked={gender === "Male"}
            name="exampleRadios"
            id="exampleRadios1"
            value="Male"
            onChange={changeGender}
          />
          &nbsp;
          <label className="form-check-label pr-2" for="exampleRadios1">
            Male
          </label>
          <input
            className="pr-2"
            type="radio"
            name="exampleRadios"
            checked={gender === "Female"}
            id="exampleRadios2"
            value="Female"
            onChange={changeGender}
          />
          <label className="form-check-label px-2" for="exampleRadios2">
            Female
          </label>
        </div>
        <div className="form-group">
          <label for="username">Hobby</label>
          <br />
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              name={"Sport"}
              value={"Sport"}
              checked={hobby[0]}
              id={`custom-checkbox-${0}`}
              onChange={() => changeHobby(0, "Sport")}
            />
            <label className="form-check-label" for="defaultCheck1">
              Sport
            </label>
          </div>
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              name={"Reading"}
              value={"Reading"}
              checked={hobby[1]}
              id={`custom-checkbox-${1}`}
              onChange={() => changeHobby(1, "Reading")}
            />
            <label className="form-check-label" for="defaultCheck1">
              Reading
            </label>
          </div>
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              name={"Music"}
              value={"Music"}
              checked={hobby[2]}
              id={`custom-checkbox-${2}`}
              onChange={() => changeHobby(2, "Music")}
            />
            <label className="form-check-label" for="defaultCheck1">
              Music
            </label>
          </div>
        </div>
        <div className="form-group">
          {/* <RangePicker
          defaultValue={{startDate: Date, endDate: Date}}
           /> */}
          <label for="username">Age</label>
          <br />
          <input
            type="range"
            min="18"
            max="55"
            id="myRange"
            value={age}
            onChange={changeAge}
          />
          &nbsp;{age}
        </div>
        <div className="form-group">
          <label for="username">Date of Birth</label>
          <br />
          <DatePicker value={dob} onChange={changeDob} />
        </div>
        <div className="form-group">
          <label for="taskname">Task Name</label>

          <input
            type="text"
            className="form-control"
            id="taskname"
            placeholder="Task Name"
            value={taskName}
            onChange={changeTaskName}
          ></input>
        </div>
        <div className="form-group">
          <label for="taskname">Status</label>

          <Select
            ref={statusInputRef}
            defaultValue={status}
            options={options}
            onChange={changeStatus}
          />
        </div>
        <button
          type="submit"
          className="btn btn-primary"
          onClick={handleSubmit}
        >
          Submit
        </button>
      </form>
    </>
  );
};
export default Todo;
