import React from "react";

const ReadOnlyRow = ({
  index,
  contact,
  handleEditClick,
  handleDeleteClick,
}) => {
  return (
    <>
      <tr key={index}>
        <td>{index + 1}</td>
        <td>{contact.username}</td>
        <td>{contact.gender}</td>
        <td>{contact.hobby}</td>
        <td>{contact.age}</td>
        <td>{contact.dob}</td>
        <td>{contact.taskName}</td>
        <td>{contact.status}</td>
        <td>
          <button
            className="btn btn-primary mr-3"
            onClick={(event) => handleEditClick(event, index, contact)}
          >
            Edit
          </button>
          <button
            className="btn btn-danger"
            onClick={() => handleDeleteClick(index)}
          >
            Delete
          </button>
        </td>
      </tr>
    </>
  );
};

export default ReadOnlyRow;
