import React, { useState } from "react";
import DatePicker from "react-date-picker";
import Select from "react-select";
const EditableRow = ({
  index,
  editFormData,
  handleEditFormChange,
  handleCancelClick,
  setEditdata,
}) => {
  const options = [
    { value: "Active", label: "Active" },
    { value: "Inactive", label: "Inactive" },
  ];
  debugger;
  const initialDob =
    editFormData.dob != null ? new Date(editFormData.dob) : null;
  const [dob, setDob] = useState(initialDob);
  const [status, setStatus] = useState(editFormData.status);
  const [gender, setGender] = useState([
    editFormData.gender === "Male" ? true : false,
    editFormData.gender === "Female" ? true : false,
  ]);
  const [hobby, setHobby] = useState([
    editFormData.hobby.includes("Sport"),
    editFormData.hobby.includes("Reading"),
    editFormData.hobby.includes("Music"),
  ]);
  const changeDob = (Date) => {
    setDob(Date);
    const nDate = Date !== null ? Date.toDateString() : null;
    const newFormData = { ...editFormData };
    newFormData["dob"] = nDate;
    setEditdata(newFormData);
  };
  const changeGender = (e) => {
    setGender([
      e.target.value === "Male" ? true : false,
      e.target.value === "Female" ? true : false,
    ]);
    const newFormData = { ...editFormData };
    newFormData["gender"] = e.target.value;
    setEditdata(newFormData);
  };
  const changeStatus = (e) => {
    setStatus(e?.value);
    handleEditFormChange(e);
  };
  const changeHobby = (position, name) => {
    const updatedHobby = hobby.map((item, index) =>
      index === position ? !item : item
    );
    setHobby(updatedHobby);
    const newFormData = { ...editFormData };
    const value = [];
    updatedHobby.forEach((item, index) => {
      if (item) {
        if (index === 0) value[0] = "Sport";
        if (index === 1) value[1] = "Reading";
        if (index === 2) value[2] = "Music";
      }
    });
    const hobbyvalue = value.filter((x) => x.length !== 0);
    newFormData["hobby"] = hobbyvalue.join(", ");
    setEditdata(newFormData);
  };

  return (
    <tr key={index}>
      <td>{index + 1}</td>
      <td>
        <input
          type="text"
          required="required"
          placeholder="Enter a Username..."
          name="username"
          value={editFormData.username}
          onChange={handleEditFormChange}
        ></input>
      </td>
      <td>
        <div style={{ display: "flex" }} key={index}>
          <div style={{ width: "58px" }} key={index}>
            <input
              className="pr-2"
              type="radio"
              checked={gender[0]}
              name="gender"
              id="exampleRadios1"
              value="Male"
              onChange={changeGender}
            />
            <label className="form-check-label pr-2" for="exampleRadios1">
              Male
            </label>
          </div>
          <div style={{ width: "75px" }} key={index}>
            <input
              className="pr-2"
              type="radio"
              name="gender"
              checked={gender[1]}
              id="exampleRadios2"
              value="Female"
              onChange={changeGender}
            />
            <label className="form-check-label pr-2" for="exampleRadios1">
              Female
            </label>
            &nbsp;
          </div>
        </div>
      </td>
      <td>
        <div style={{ display: "flex" }} key={index}>
          <div style={{ paddingRight: "10px" }} key={index}>
            <input
              className="form-check-input"
              type="checkbox"
              name="hobby"
              value={"Sport"}
              checked={hobby[0]}
              // checked={editFormData.hobby.includes("Sport")}
              id={`custom-checkbox-${0}`}
              onChange={() => changeHobby(0, "Sport")}
            />
            <label className="form-check-label" for="defaultCheck1">
              Sport
            </label>
            &nbsp;
          </div>
          <div
            className="form-check"
            style={{ paddingRight: "10px" }}
            key={index}
          >
            <input
              className="form-check-input"
              type="checkbox"
              name="hobby"
              value={"Reading"}
              checked={hobby[1]}
              // checked={editFormData.hobby.includes("Reading")}
              id={`custom-checkbox-${1}`}
              onChange={() => changeHobby(1, "Reading")}
            />
            <label className="form-check-label" for="defaultCheck1">
              Reading
            </label>
            &nbsp;
          </div>
          <div className="form-check" key={index}>
            <input
              className="form-check-input"
              type="checkbox"
              name="hobby"
              value={"Music"}
              checked={hobby[2]}
              // checked={editFormData.hobby.includes("Music")}
              id={`custom-checkbox-${2}`}
              onChange={() => changeHobby(2, "Music")}
            />
            <label className="form-check-label" for="defaultCheck1">
              Music
            </label>
          </div>
        </div>
      </td>
      <td>
        <input
          type="range"
          min="18"
          max="55"
          id="myRange"
          name="age"
          value={editFormData.age}
          onChange={handleEditFormChange}
        />
        &nbsp;{editFormData.age}
      </td>
      <td>
        <DatePicker value={dob} onChange={changeDob} />
      </td>
      <td>
        <input
          type="text"
          placeholder="Enter a TaskName"
          name="taskName"
          value={editFormData.taskName}
          onChange={handleEditFormChange}
        />
      </td>
      <td>
        <Select
          value={{ label: status, value: status }}
          options={options}
          onChange={changeStatus}
        />
      </td>
      <td>
        <div style={{ display: "flex" }}>
          <button className="btn btn-primary mr-3" type="submit">
            Save
          </button>
          <button
            className="btn btn-secondary mr-3"
            type="button"
            onClick={handleCancelClick}
          >
            Cancel
          </button>
        </div>
      </td>
    </tr>
  );
};

export default EditableRow;
