import { Fragment, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { DeleteOne, UpdateContact } from "../actions";
import { confirmAlert } from "react-confirm-alert"; // Import
import "react-confirm-alert/src/react-confirm-alert.css"; // Import css
import EditableRow from "../components/EditableRow";
import ReadOnlyRow from "../components/ReadOnlyRow";

const ContactDataTable = () => {
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);
  const [editContactId, setEditContactId] = useState(null);
  const [editFormData, setEditFormData] = useState({
    userName: "",
    gender: "",
    hobby: "",
    age: "",
    dob: "",
    taskName: "",
    status: "",
  });

  const handleUpdate = (event, index, contact) => {
    event.preventDefault();
    setEditContactId(index);
    const formValues = {
      username: contact.username,
      gender: contact.gender,
      hobby: contact.hobby,
      age: contact.age,
      dob: contact.dob,
      taskName: contact.taskName,
      status: contact.status,
    };
    setEditFormData(formValues);
  };

  const handleCancelClick = () => {
    setEditContactId(null);
  };

  const handleDelete = (index) => {
    confirmAlert({
      customUI: ({ onClose }) => {
        return (
          <div
            style={{
              textAlign: "center",
              width: "500px",
              padding: "40px",
              background: "#28bae6",
              boxShadow: "0 20px 75px rgba(0, 0, 0, 0.23)",
              color: "#fff",
            }}
          >
            <h1>Are you sure?</h1>
            <p>You want to delete this Contact?</p>
            <button
              onClick={onClose}
              style={{
                width: "160px",
                padding: "10px",
                border: "1px solid #fff",
                margin: "10px",
                cursor: "pointer",
                background: "none",
                color: "#fff",
                fontSize: "14px",
              }}
            >
              No
            </button>
            <button
              onClick={() => {
                dispatch(DeleteOne(index));
                onClose();
              }}
              style={{
                width: "160px",
                padding: "10px",
                border: "1px solid #fff",
                margin: "10px",
                cursor: "pointer",
                background: "none",
                color: "#fff",
                fontSize: "14px",
              }}
            >
              Yes, Delete it!
            </button>
          </div>
        );
      },
    });
  };

  const handleEditFormChange = (event) => {
    const value = event.value;
    if (value === "Inactive" || value === "Active") {
      const fieldName = "status";
      const fieldValue = value;
      const newFormData = { ...editFormData };
      newFormData[fieldName] = fieldValue;
      setEditFormData(newFormData);
    } else {
      event.preventDefault();
      const fieldName = event.target.getAttribute("name");
      const fieldValue = event.target.value;
      const newFormData = { ...editFormData };
      newFormData[fieldName] = fieldValue;
      setEditFormData(newFormData);
    }
  };
  const setEditdate = (data) => {
    ;
    setEditFormData(data);
  };
  const handleEditFormSubmit = (event) => {
    event.preventDefault();
    const editedContact = {
      username: editFormData.username,
      gender: editFormData.gender,
      hobby: editFormData.hobby,
      age: editFormData.age,
      dob: editFormData.dob,
      taskName: editFormData.taskName,
      status: editFormData.status,
    };
    dispatch(UpdateContact(editedContact, editContactId));
    setEditContactId(null);
  };
  return (
    <>
      <h2 className="p-3 mb-2 bg-primary text-white text-center rounded">
        Contacts List
      </h2>
      <div className="mt-5">
        <form onSubmit={handleEditFormSubmit}>
          <table className="table">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">User Name</th>
                <th scope="col">Gender</th>
                <th scope="col">Hobby</th>
                <th scope="col">Age</th>
                <th scope="col">Date of Birth</th>
                <th scope="col">Task Name</th>
                <th scope="col">Status</th>
                <th scope="col">Action</th>
              </tr>
            </thead>
            <tbody>
              {user.map((item, index) => (
                <Fragment>
                  {editContactId === index ? (
                    <EditableRow
                      key={index}
                      index={index}
                      editFormData={editFormData}
                      handleEditFormChange={handleEditFormChange}
                      setEditdata={setEditdate}
                      handleCancelClick={handleCancelClick}
                    />
                  ) : (
                    <ReadOnlyRow
                      key={index}
                      index={index}
                      contact={item}
                      handleEditClick={handleUpdate}
                      handleDeleteClick={handleDelete}
                    />
                  )}
                </Fragment>
              ))}
            </tbody>
          </table>
        </form>
      </div>
    </>
  );
};
export default ContactDataTable;
