import { ADD_DATA, DELETE_ONE, UPDATE_DATA } from "../types";

export const AddContact = (contact) => {
  ;
  return (dispatch) => {
    return dispatch({ type: ADD_DATA, contact });
  };
};
export const UpdateContact = (contact, index) => {
  ;
  return (dispatch) => {
    return dispatch({ type: UPDATE_DATA, contact, index });
  };
};
export const DeleteOne = (index) => {
  ;
  return (dispatch) => {
    return dispatch({ type: DELETE_ONE, index });
  };
};
