import { ADD_DATA, DELETE_ONE, UPDATE_DATA } from "../types";
const initialstate = {
  user: [
    {
      username: "Hitesh",
      gender: "Male",
      hobby: "Sport, Reading,",
      age: "33",
      dob: "Thu Feb 10 2022",
      taskName: "todo-task-1",
      status: "Active",
    },
  ],
};
const reducer = (state = initialstate, action) => {
  switch (action.type) {
    case ADD_DATA:
      return {
        ...state,
        user: [...state.user, action.contact],
      };
    case UPDATE_DATA:
      ;
      return {
        ...state,
        user: state.user.map((contact, index) => {
          return index === action.index ? { ...action.contact } : contact;
        }),
      };
    case DELETE_ONE:
      ;
      return {
        ...state,
        user: state.user.filter((contact, index) => {
          return index !== action.index;
        }),
      };
    default:
      return state;
  }
};
export default reducer;
